<!--eslint-disable-->
<template>
  <div class="formacoes-wrapper">
    <div class="formacoes">
      <div class="cards-wrapper movement">
        <div
          class="card"
          v-for="f in formacoes"
          :key="f.id"
          :class="{ 'is-visible': f.is_visible == 1 }"
        >
          <div
            class="card-img"
            :style="{ 'background-image': 'url(' + f.img + ')' }"
          ></div>
          <div class="card-content">
            <div class="card-header">
              <div class="card-type">{{ f.categoria }}</div>
              <div
                class="card-status"
                :class="{
                  'is-full': f.is_full == 1,
                  'has-vagas': f.last_vagas == 1,
                }"
              >
                <span :class="{ 'is-full': f.is_full == 1 }"
                  >vagas preenchidas</span
                >
                <span :class="{ 'has-vagas': f.last_vagas == 1 }"
                  >últimas vagas</span
                >
              </div>
            </div>
            <div class="card-body">
              <h3 class="card-title">{{ f.title }}</h3>
              <div class="card-date">
                <div class="txt">
                  {{ f.start }}
                  <br />
                  {{ f.end }}
                </div>
                <div
                  class="icon"
                  :style="{ 'background-image': 'url(' + calendar + ')' }"
                ></div>
              </div>
            </div>
          </div>
          <router-link :to="f.url">
            <div class="card-cta">
              <div class="txt">Saiba mais</div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import calendar from "../../assets/calendar.svg";
import moment from "moment";

export default {
  data() {
    return {
      calendar,
      formacoes: [
        {
          id: "",
          code: "",
          title: "",
          text: "",
          url: "",
          img: "",
          start: "",
          end: "",
          is_full: "",
          last_vagas: "",
          is_visible: "",
          categoria: "",
        },
      ],
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFormacaoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var formacoesArray = [];

        map.call(data, function (f) {
          var src = f.icon.split("uploads");
          var ds;
          var de;
          if (f.date_start == "0000-00-00") {
            ds = "disponível brevemente";
            de = "";
          } else {
            ds = moment(f.date_start).format("DD-MM-YYYY");
            de = moment(f.date_end).format("DD-MM-YYYY");
          }

          var categoria;
          if (f.categoria_id == 1) categoria = "formacao";
          else if (f.categoria_id == 2) categoria = "curso";
          else categoria = "webinar";

          var categoria_title;
          if (f.categoria_id == 1) categoria_title = "formação";
          else if (f.categoria_id == 2) categoria_title = "curso";
          else categoria_title = "webinar";

          formacoesArray.push({
            id: f.id,
            code: f.code,
            title: f.title,
            text: f.text,
            url: "/academia/" + categoria + f.link,
            img: "https://beta.anacarolinapereira.pt/uploads" + src[1],
            start: ds,
            end: de,
            is_full: f.is_full,
            last_vagas: f.last_vagas,
            is_visible: f.is_visible,
            categoria: categoria_title,
          });
        });

        vm.$data.formacoes = formacoesArray;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.formacoes-wrapper {
  display: flex;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  .formacoes {
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: pxToEm(1500);
    margin: auto;
    position: relative;

    // @media only screen and (max-width: 991px) {
    //   display: none;
    // }

    .cards-wrapper {
      display: flex;
      max-width: pxToEm(1200);
      width: 100%;
      justify-content: space-between;
      height: 55%;
      flex-wrap: wrap;
      margin: auto;
      margin-bottom: pxToEm(80);

      @media only screen and (min-width: 300px) and (max-width: 991px) {
        flex-direction: column;
        height: 100%;
        align-items: center;
        margin-bottom: pxToEm(40);
      }

      .card {
        width: pxToEm(500);
        height: pxToEm(530);
        background-color: $white;
        border-radius: pxToEm(43);
        display: none;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        transition: 0.5s;
        margin: pxToEm(50) pxToEm(10) pxToEm(50) pxToEm(10);
        box-shadow: pxToEm(3) pxToEm(3) pxToEm(15) rgba(0, 0, 0, 0.2);
        position: relative;

        @media only screen and (min-width: 300px) and (max-width: 599px) {
          width: 90%;
        }

        &.is-visible {
          display: flex;
        }

        .card-img {
          background-color: #cbcbcb;
          border-radius: pxToEm(43);
          width: 100%;
          height: 70%;
          background-size: cover;
          background-position: center;
        }

        .card-content {
          display: flex;
          flex-direction: column;
          padding: pxToEm(30) pxToEm(30);
          height: 30%;
          width: 88%;

          @media only screen and (min-width: 300px) and (max-width: 599px) {
            width: 90%;
          }

          .card-header {
            display: flex;
            justify-content: space-between;

            .card-type {
              text-transform: uppercase;
              color: $turquiose;
              font-family: "Arial";
              font-size: pxToEm(20);
              font-weight: 600;
            }

            .card-status {
              width: pxToEm(200);
              border: pxToEm(1) solid $turquiose;
              border-radius: pxToEm(20);
              text-transform: uppercase;
              font-family: "Arial";
              align-items: center;
              justify-content: center;
              display: none;

              &.is-full {
                background-color: $white;
                color: $turquiose;
                display: flex;

                span {
                  display: none;

                  &.is-full {
                    display: block;
                  }
                }
              }

              &.has-vagas {
                background-color: $turquiose;
                color: $white;
                display: flex;

                span {
                  display: none;

                  &.has-vagas {
                    display: block;
                  }
                }
              }
            }
          }

          .card-body {
            display: flex;
            justify-content: space-between;
            margin-top: pxToEm(15);

            .card-title {
              margin: 0;
              font-family: "Arial";
              color: #707070;
              font-weight: bold;
              font-size: pxToEm(22);
              max-width: 65%;
              width: 100%;
              height: pxToEm(76);
            }

            .card-date {
              display: flex;
              justify-content: space-between;
              width: 40%;
              text-align: right;

              .txt {
                width: 100%;
                margin-right: pxToEm(10);
              }

              .icon {
                // width: pxToEm(52);
                // height: pxToEm(41);
                width: 30px;
                height: 22px;
                background-size: contain;
                background-repeat: no-repeat;
              }
            }
          }
        }

        a {
          position: absolute;
          width: pxToEm(220);
          height: pxToEm(55);
          bottom: pxToEm(-27.5);

          .card-cta {
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $turquiose;
            border: pxToEm(1) solid $turquiose;
            border-radius: pxToEm(20);
            transition: 0.5s;
            cursor: pointer;

            .txt {
              color: #fff;
              font-size: pxToEm(22);
            }

            &:hover {
              background-color: $white;

              .txt {
                color: $turquiose;
              }
            }
          }
        }
      }
    }
  }
}
</style>



