<!-- eslint-disable -->
<template>
  <div class="apresentacao-wrapper movement">
    <div>
      <div class="img">
        <div :style="{ 'background-image': 'url(' + img + ')' }"></div>
      </div>
      <div id="apresentacao">
        <h2 class="title">A Academia</h2>
        <div class="line"></div>
        <div class="copy" v-html="text">
          Texto de apresentação para as formações.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
import img from "../../assets/illustrations/garca.svg";

export default {
  data() {
    return {
      img: img,
      text: "",
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFormacoes.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[1]);

        vm.$data.text = intro[0].text;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.apresentacao-wrapper {
  background-color: #fafafa;
  height: pxToEm(535);
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
  }

  > div {
    max-width: pxToEm(1500);
    margin: auto;
    width: 100%;
    position: relative;
    height: 100%;

    .img {
      width: 20%;
      height: 100%;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;

      @media only screen and (max-width: 599px) {
        display: none;
      }

      > div {
        width: 100%;
        height: 80%;
        position: relative;
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    #apresentacao {
      height: 100%;
      margin: $mg-desktop;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .title {
        margin: 0;
        font-weight: normal;
        color: $turquiose;
        font-size: pxToEm(34);
        margin-bottom: pxToEm(10);
      }

      .line {
        width: pxToEm(70);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-bottom: pxToEm(30);
      }

      .copy {
        color: #8e8e8e;
        font-size: pxToEm(22);
        max-width: pxToEm(755);
        width: 100%;
        text-align: justify;
        text-align-last: center;
      }
    }
  }
}
</style>



