<template>
  <div>
    <banner></banner>
    <apresentacao></apresentacao>
    <formacoes></formacoes>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./academia/Banner";
import Apresentacao from "./academia/Apresentacao";
