        },
        {
          property: "og:url",
          content: "https://beta.anacarolinapereira.pt/academia",
        },
        {
          property: "og:description",
          content:
            "A Academia. Pretende ser um espaço de conhecimento. De descoberta e exploração. De um horizonte aberto e infinito. Com capacidade de olhar desde o alto. De liberdade, portanto. De observar e aprender. Como uma garça. No fundo, será isso: a Academia como um espaço que nos possibilite voar para o infinito, pelo que aprendemos e pelo que partilhamos. Com e para pessoas. Com e para curiosos por aprender. E partilhar.",
